const TPL = require( './components/tpl');

jQuery(document).ready( function(){

        window.setTimeout(() => {
            
                var user_data = { 
                    product_id : wvsap_ajax.product_id,
                    cats : wvsap_ajax.cats
                };

                var formData = new FormData();
                formData.append( "action", 'wvsap_ajax' );
                formData.append( "method", 'frontend\\functions\\AdjacentProducts@single_page_adjacent' );
                formData.append( "cs_token", wvsap_ajax.nonce );
                TPL.objectToFormData( formData, user_data )

                jQuery.ajax({
                    url: wvsap_ajax.url,
                    type: 'POST',
                    data: formData,
                    contentType: false,
                    cache: false,
                    processData: false,
                })
                .done(function( data ){
                    // console.log(  data );

                    if( data !== false && data.items != '' ){

                        for (const [key, value] of Object.entries(data.items)) {
                            var pitems = '';
                            value.forEach( e => {
                                // console.log( e.purl );
                                pitems  += '<li class="item"><a href="' + e.purl + '" target="_blank">'
                                        + '<img src="' + e.img_src + '" width="150" >'
                                        + '<strong>' + e.pname + '</strong><br>'
                                        + e.price
                                        + '</a></li>';
                            });

                            window[ 
                                'ait_' + key
                            ] = pitems;

                        }
                        
                    }
                })
                .fail(function( errorThrown ){
                    console.log( 'Error: ' + errorThrown.responseText );
                });
                

        }, 1000 ); //load data after page load

    
    jQuery(".variable-item, .swatch-color, .vi-wpvs-option-wrap, .rtwpvs-term, .swatch-color, .thwvsf-wrapper-item-li, .swatch").on('click', function(){
        var currentValue = jQuery(this).data( 'value' );
        if( typeof currentValue === 'undefined' ){
            var currentValue = jQuery(this).data( 'attribute_value' );
        }

        if( typeof currentValue === 'undefined' ){
            var currentValue = jQuery(this).data( 'term' );
        }

        // console.log(
        //     currentValue
        // );

        if( typeof window[ 'ait_' + currentValue ] === 'undefined' ){
            return false;
        }

        jQuery('.wpsvs').remove();
        jQuery('body').append( TPL.spc( 'Same Color - Different Design',  window[ 'ait_' + currentValue ] ) );    
        jQuery('.items').slick({
            slidesToShow: 3,
            slidesToScroll: 4,
            autoplay: true,
            autoplaySpeed: 3000,
        });

    });

    jQuery("body").on( 'click', '.close-sugs', function(){
        jQuery('.wpsvs').hide('slow', function(){
            jQuery(this).remove();
        })
    });

});