
var TPL = {

    spc : function( title, items ){
        return '<div class="wpsvs variation-swatches-similar-products"><h3 class="title">'+title+' <span class="close-sugs">X</span></h3><ul class="items">'+items+'</ul></div>';
    },
    item: function( args ){
        var item = '';
        for (const [key, value] of Object.entries(args)) {
            // console.log( value['thumb'][0] );
            item += '<li class="item">'+
                '<img src="'+value['thumb'][0]+'" height="150" >'+
            '</li>';
        }
        return item;
    },
    objectToFormData : function( formIns, user_data, fkey = false ){
        if( typeof user_data !== 'object' ){
            return user_data;
        }
        for (const [key, value] of Object.entries(user_data)) {
            // console.log(`${key}: ${value}`);
            if( typeof value === 'object' ){
                TPL.objectToFormData( formIns, value, key );
            }else{
                if( fkey !== false ){
                    formIns.append( fkey +'['+ key +']', value );
                }else{
                    formIns.append( key, value );
                }
            }
        }

        return formIns;
    }

};


module.exports = TPL;